.TodoItem{
   padding: .5em;
   display: -ms-flexbox;
   display: flex;
   -ms-flex-align: center;
       align-items: center;
   margin: 8px 0 8px 0;
   border-bottom: 1px solid rgb(230,230,230);
   cursor: pointer;
 }
 
 .TodoItem:hover{
   background: rgb(238,255,221);
   -webkit-transition: 1s;
   -o-transition: 1s;
   transition: 1s;
 }
 .TodoItem>.title{
   -ms-flex-positive: 1;
       flex-grow: 1;
   padding: 0 .5em;
 }
 .TodoItem .icon-delete1{
   border:none;
   cursor: pointer;
   background: none;
   color:black;
   font-size: 20px;
 }
 .TodoItem .icon-delete1:hover{  
   -webkit-transform: scale(1.4,1.4);  
       -ms-transform: scale(1.4,1.4);  
           transform: scale(1.4,1.4);
   -webkit-transition: 0.5s;
   -o-transition: 0.5s;
   transition: 0.5s;
}


